<template>
	<div id="vip-card-bag">
		<public-header title="会员卡包"></public-header>
		<div class="content bg-default font-14">
			<template v-if="cards_mine.length > 0">
				<div class="headline">我的会员卡</div>
				<ul class="mycard padding-15-lr" >
					<template v-for="(item, index) in cards_mine">
						<template v-if="showAllMine">
							<li class="mycard-item" :style="{background: `url(${item.thumb}) no-repeat top center`, backgroundSize: '100% auto'}">
								<div class="store-logo">
									<img class="img-100" :src="item.store_logo" alt="">
								</div>
								<div class="store-info">
									<div class="store-title ellipsis">{{item.store_title}}</div>
									<div class="font-12">{{item.group_title}}</div>
								</div>
								<router-link tag="div" class="vip-detail" :to="util.getUrl({path: '/plugin/pages/kabao/detail', query: {sid: item.sid}})">会员详情</router-link>
							</li>
						</template>
						<template v-else>
							<li class="mycard-item" :style="{background: `url(${item.thumb}) no-repeat top center`, backgroundSize: '100% auto'}" v-if="index < 4">
								<div class="store-logo">
									<img class="img-100" :src="item.store_logo" alt="">
								</div>
								<div class="store-info">
									<div class="store-title ellipsis">{{item.store_title}}</div>
									<div class="font-12">{{item.group_title}}</div>
								</div>
								<router-link tag="div" class="vip-detail" :to="util.getUrl({path: '/plugin/pages/kabao/detail', query: {sid: item.sid}})">会员详情</router-link>
							</li>
						</template>
					</template>
				</ul>
				<div class="flex-center margin-15-t c-gray" v-if="cards_mine.length > 4" @click="onToggleShowAll">
					<template v-if="showAllMine">
						<span class="margin-5-r">收起</span>
						<i class="icon icon-fold "></i>
					</template>
					<template v-else>
						<span class="margin-5-r">展开</span>
						<i class="icon icon-unfold"></i>
					</template>
				</div>
			</template>
			<div class="headline">附近商家</div>
			<van-list
				v-model="records.loading"
				:finished="records.finished"
				@load="onLoad"
				:offset="100"
				:immediate-check="false"
				v-if="!records.empty"
				class="nearby-stores padding-15-lr"
			>
				<li class="nearby-store-item" v-for="(item, index) in records.data" :key="index">
					<div class="store padding-15-tb">
						<div class="store-logo">
							<img class="img-100" :src="item.logo" alt="">
						</div>
						<div class="store-info">
							<div class="store-title w-100 ellipsis font-bold">{{item.title}}</div>
							<ul class="power">
								<li class="power-item" v-for="(power, powerIndex) in item.powers">{{power}}</li>
							</ul>
						</div>
						<router-link tag="div" class="join flex" :to="util.getUrl({path: '/plugin/pages/kabao/detail', query: {sid: item.sid}})">
							<i class="icon icon-plus font-12"></i>
							<span>免费加入</span>
						</router-link>
					</div>
					<div class="van-hairline--top padding-15-tb" @click="util.jsLocation({lat: item.location_x, lng: item.location_y})">
						<van-row>
							<van-col span="18">
								<div class="flex w-100">
									<i class="icon icon-location margin-5-r"></i>
									<span class="store-address ellipsis">{{item.address}}</span>
								</div>
							</van-col>
							<van-col span="5">
								<div class="padding-10-l van-hairline--left">{{item.distance > 100 ? '>100' : item.distance}}km</div>
							</van-col>
							<van-col span="1">
								<i class="icon icon-right text-right font-12 c-gray"></i>
							</van-col>
						</van-row>
					</div>
				</li>
				<load type="loaded" text="更多商家会员，敬请期待" v-if="records.finished"></load>
			</van-list>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import load from '@/components/load'
	import {mapState, mapMutations} from 'vuex'
	import {getRecords} from '@/controller/funcCommon.js'
	export default {
		data() {
			return {
				cards_mine: [],
				records: {
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data: []
				},
				showAllMine: true,
				getLocationFail: false,
				showPreLoading: true
			}
		},
		components: {
			PublicHeader,
			load
		},
		methods: {

			onLoad() {
				var that = this;
				getRecords({
					vue: that,
					url: 'kabao/index/index',
					recordsName: 'cards_store',
					data: {
						page: that.records.page,
						psize: that.records.psize,
						lat: that.locationInfo.location_x,
						lng: that.locationInfo.location_y,
					},
					success: (res) => {
						that.cards_mine = [...res.cards_mine];
						if(that.cards_mine.length > 4) {
							that.showAllMine = false;
						}
					},
				})
			},

			onToggleShowAll() {
				var that = this;
				that.showAllMine = !that.showAllMine;
			},

			onInit() {
				var that = this;
				that.getLocation();
				if(!that.locationInfo.location_x) {
					that.util.getLocation({
						successLocation: function(res) {
							that.setLocation({
								location_x: res.location_x,
								location_y: res.location_y
							});
							that.onLoad();
						},
						successAddress:  function(res) {
							that.setLocation({
								location_x: res.location_x,
								location_y: res.location_y,
								address: res.address
							});
						},
						fail: function(res) {
							that.setLocation({
								last_location_x: 0,
								location_x: 0,
								address: '获取定位失败'
							});
							that.getLocationFail = true;
							that.onLoad();
						}
					});
				} else {
					that.getLocationFail = false;
					that.locationInfo.last_location_x = this.locationInfo.location_x;
					that.onLoad();
				}
			},

			...mapMutations([
				'setLocation', 'getLocation'
			]),
		},
		mounted() {
			this.onInit();
		},
		computed: {
			...mapState([
				'locationInfo'
			]),
		},
	}
</script>

<style>
	#vip-card-bag .headline{
		padding: 15px;
		font-size: 15px;
		font-weight: bold;
	}
	#vip-card-bag .mycard{
		padding: 0 15px;
		color: #fff;
	}
	#vip-card-bag .mycard .mycard-item{
		position: relative;
		padding: 20px 20px 25px 20px;
		margin: 15px 0;
		border-radius: 15px 15px 5px 5px;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#vip-card-bag .mycard .mycard-item:first-child{
		margin-top: 0px;
	}
	#vip-card-bag .mycard .mycard-item:last-child{
		margin-bottom: 0px;
	}
	#vip-card-bag .mycard .mycard-item .store-logo{
		width: 40px;
		height: 40px;
		border-radius: 5px;
		overflow: hidden;
	}
	#vip-card-bag .mycard .mycard-item .store-info{
		position: absolute;
		top: 20px;
		left: 75px;
		right: 123px;
		height: 40px;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#vip-card-bag .mycard .mycard-item .store-info .store-title{
		width: 100%;
		font-weight: bold;
		font-size: 15px;
	}
	#vip-card-bag .mycard .mycard-item .vip-detail{
		padding: 7px 15px;
		border-radius: 100px;
		background-color: rgba(0, 0, 0, 0.1);
		border: 1px solid #ccc;
	}
	#vip-card-bag .nearby-store-item{
		padding: 0 15px;
		margin: 15px 0;
		border-radius: 10px;
		box-shadow: 1px 1px 10px #999;
	}
	#vip-card-bag .nearby-store-item .store{
		position: relative;
	}
	#vip-card-bag .nearby-store-item:first-child{
		margin-top: 0px;
	}
	#vip-card-bag .nearby-store-item:last-child{
		margin-bottom: 0px;
	}
	#vip-card-bag .nearby-store-item .store-logo{
		position: absolute;
		top: 15px;
		left: 0;
		width: 40px;
		height: 40px;
		border-radius: 5px;
		overflow: hidden;
	}
	#vip-card-bag .nearby-store-item .store-address{
		max-width: 90%;
	}
	#vip-card-bag .nearby-store-item .join{
		position: absolute;
		top: 20px;
		right: 0px;
		padding: 8px 15px;
		border-radius: 100px;
		background-color: #ff2d4b;
		color: #fff;
	}
	#vip-card-bag .nearby-store-item .store-info{
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: space-between;
		margin: 0 110px 0 50px;
		min-height: 40px;
	}
	#vip-card-bag .nearby-store-item .store-info .power{
		display: flex;
		flex-flow: row wrap;
	}
	#vip-card-bag .nearby-store-item .store-info .power .power-item{
		padding: 3px 8px;
		border: 1px solid rgba(255, 45, 75, 0.3);
		border-radius: 50px;
		margin: 5px 5px 0 0;
		font-size: 12px;
		color: #ff2d4b;
	}
	#vip-card-bag .tongcheng-stores{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		padding: 0 10px;
	}
	#vip-card-bag .tongcheng-stores .tc-store-item{
		width: 25%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		padding-bottom: 15px;
	}
	#vip-card-bag .tongcheng-stores .tc-store-item .store-logo{
		width: 50px;
		height: 50px;
		border-radius: 5px;
		overflow: hidden;
	}
	#vip-card-bag .tongcheng-stores .tc-store-item .store-title{
		max-width: 90%;
		font-size: 12px;
		margin-top: 5px;
	}
</style>



